<template>
    <div class="music-list">
        <div class="back">
            <i class="icon-back" @click="back"></i>
        </div>
        <h1 class="title" v-html="title"></h1>
        <div class="bg-image" :style="bgStyle" ref="bgImage">
            <div class="play-wrapper">
                <div class="play" v-show="songs.length>0" ref="playBtn" @click="random">
                   <i class="icon-play"></i>
                   <span class="text">随机播放全部</span>
                </div>
            </div>
            <div class="filter" ref="filter"></div>
        </div>
        <div class="bg-layer" ref="layer"></div>
        <Scroll :data="songs" class="list" ref="list" 
                :listen-scroll="listenScroll" :probe-type="probeType" @scroll="scroll">
            <div class="song-list-wrapper">
                <song-list :rank="rank" :songs="songs" @select="selectItem"></song-list>
            </div>
            <div class="loading-container" v-show="!songs.length">
                <Loading></Loading>
            </div>
        </Scroll>
    </div>
</template>

<script>
import Scroll from 'base/scroll/scroll'
import SongList from 'base/song-list/song-list'
import Loading from 'base/loading/loading'
import {mapActions} from  'vuex'
import {playListMixin} from 'common/js/mixin'

const RESERVED_HEIGHT = 40

    export default {
        mixins:[playListMixin],
        props: {
            bgImage: {
                type: String,
                default: ''
            },
            songs: {
                type: Array,
                default: []
            },
            title: {
                type: String,
                default: ''
            },
            rank: {
                type: Boolean,
                default: false
            }
        },
        data() {
            return {
                scrollY: 0
            }
        },
        computed: {
            bgStyle() {
                return `background-image:url(${this.bgImage})`
            },
        },
        mounted() {
            this.imageHeight = this.$refs.bgImage.clientHeight // 保存图片的高度
            this.minTransalteY = -this.imageHeight + RESERVED_HEIGHT //设置滚动的最大高度
            this.$refs.list.$el.style.top = `${this.imageHeight}px`
        },
        created() {
            this.probeType = 3
            this.listenScroll = true
        },
        methods: {
            handlePlayList(playList) {
                const bottom = playList.length>0 ? '60px' : ''
                this.$refs.list.$el.style.bottom = bottom
                this.$refs.list.refresh()
            },
            scroll(pos) {
                this.scrollY = pos.y
                //console.log(this.newY)
            },
            back() {
                this.$router.back()
            },
            selectItem(item, index) {
                //pan duan bo fang mo shi 
                this.selectPlay({
                    list: this.songs,
                    index
                })
            },
            random() {
                this.randomPlay({
                    list:this.songs
                })
            },
            ...mapActions([
                'selectPlay',
                'randomPlay'
            ])
        },
        watch: {
            scrollY(newY) {
                let transalteY = Math.max(this.minTransalteY,newY)
                //console.log(transalteY)
                this.$refs.layer.style.transform = `translate3d(0,${transalteY}px,0)`
                let zIndex = 0
                let scale = 1
                let blur = 0
                const percent = Math.abs(newY/this.imageHeight)
                if(newY > 0) {
                    //下拉放大，ｐｅｒｃｅｎｔ的算法保证无缝放大
                    scale = 1 + percent
                    zIndex = 10
                } else { // 上滑模糊
                    blur = Math.min(20 * percent, 20)
                }
                this.$refs.filter.style.backdrop = `blur(${blur}px)`
                if(newY < this.minTransalteY) {
                    zIndex = 10
                    this.$refs.bgImage.style.paddingTop = 0
                    this.$refs.bgImage.style.height = `${RESERVED_HEIGHT}px`
                    this.$refs.playBtn.style.display = 'none' // 隐藏随机按钮
                } else {
                    this.$refs.bgImage.style.paddingTop = `70%`
                    this.$refs.bgImage.style.height = 0
                    this.$refs.playBtn.style.display = 'block' 
                }
                this.$refs.bgImage.style.zIndex = zIndex
                this.$refs.bgImage.style.transform = `scale(${scale})`
            }
        },
        components: {
            Scroll,
            SongList,
            Loading
        }
    }
</script>

<style lang="stylus" scoped>
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"

  .music-list
    position: fixed
    z-index: 100
    top: 0
    left: 0
    bottom: 0
    right: 0
    background: $color-background
    .back
      position absolute
      top: 0
      left: 6px
      z-index: 50
      .icon-back
        display: block
        padding: 10px
        font-size: $font-size-large-x
        color: $color-theme
    .title
      position: absolute
      top: 0
      left: 10%
      z-index: 40
      width: 80%
      no-wrap()
      text-align: center
      line-height: 40px
      font-size: $font-size-large
      color: $color-text
    .bg-image
      position: relative
      width: 100%
      height: 0
      padding-top: 70%
      transform-origin: top
      background-size: cover
      .play-wrapper
        position: absolute
        bottom: 20px
        z-index: 50
        width: 100%
        .play
          box-sizing: border-box
          width: 135px
          padding: 7px 0
          margin: 0 auto
          text-align: center
          border: 1px solid $color-theme
          color: $color-theme
          border-radius: 100px
          font-size: 0
          .icon-play
            display: inline-block
            vertical-align: middle
            margin-right: 6px
            font-size: $font-size-medium-x
          .text
            display: inline-block
            vertical-align: middle
            font-size: $font-size-small
      .filter
        position: absolute
        top: 0
        left: 0
        width: 100%
        height: 100%
        background: rgba(7, 17, 27, 0.4)
    .bg-layer
      position: relative
      height: 100%
      background: $color-background
    .list
      position: fixed
      top: 0
      bottom: 0
      width: 100%
      background: $color-background
      .song-list-wrapper
        padding: 20px 30px
      .loading-container
        position: absolute
        width: 100%
        top: 50%
        transform: translateY(-50%)
</style>



